export default {
  data(){
    return{
      show:false,
      username:"",
    }
  },
  methods:{
    login(){
      this.$router.push({name:"login"})
    },
    loginout(){
      localStorage.removeItem("username");
      localStorage.removeItem("phone");
      this.username="";
      this.show=false;
    }
  },
  mounted(){
    let name=localStorage.getItem("username");
    if(name){
        this.show=true;
        this.username=name;
    }
  }
}



